












import Vue from 'vue'
import { SkillProcessor } from '../../models/skills/SkillEnums';

interface IProcessor {
    type: SkillProcessor;
    params: string[];
    desc?: string;
}

interface IData {
    procs: IProcessor[];
}

const processorMap: {[k: number /* SkillProcessor */]: (params: string[]) => string|Promise<string>} = {
    [SkillProcessor.DO_ACTION]: (params) => `执行动作 '${params[0]}'`,
    [SkillProcessor.DO_LOOPING_ACTION]: (params) =>`在 ${Number(params[3]) / 1000}秒 时间内` +  (params[4] ? ` 以 ${params[4]} 移动速度` : '') +` 循环执行动作 '${params[0]}', '${params[1]}', '${params[2]}' ` ,
    [SkillProcessor.CHANGE_PROJECTILE]: async (params) => `改变施展者 ${params[0]} to ${params[1]}`,
    [SkillProcessor.DIVIDED_STATE_EFFECT_ARG]: (params) => `分离属性效果 ${params}`,
    [SkillProcessor.IMPACT_BLOW]: () => `冲击打击`,
    [SkillProcessor.AURA]: (params) => `在以 ${params[0]} 半径单位内应用光环效果`,
    [SkillProcessor.ADD_ACTION_SUFFIX]: (params) => `往动作${params.slice(1).map((s) => "'" + s + "'").join(', ')} 加上 '${params[0]}' 后缀` ,
    [SkillProcessor.APPLY_RANGED_STATE_EFFECT]: (params) => `在${Number(params[3]) / 1000}秒内，以 ${params[1]}% 几率 对${params[0]}个目标单位内应用效果 ${params[2]} `,
    [SkillProcessor.MODIFY_ACTION_ON_BUBBLE_COUNT]: (params) => modifyOnBubbleCount(params),
    [SkillProcessor.MODIFY_STATE_EFFECT_ON_BUBBLE_COUNT]: (params) => `ModifyStateEffectOnBubbleCount ${params[0]}`,
    [SkillProcessor.SUMMON_DO_ACTION]: (params) => `召唤动作 '${params[0]}'`,
    [SkillProcessor.CTC]: (params) => `保存 ${params[0]} 次冷却时间`,
    [SkillProcessor.UNKNOWN16]: (params) => `在 ${Number(params[3]) / 1000}秒内，`+(params[4] ? ` 以 ${params[4]} 动作速度` : '')+`循环执行动作 '${params[0]}', '${params[1]}', '${params[2]}' `  + ` 与 ${params[5]} unk1 ${params[6]} unk2 ${params[7]} 发生碰撞`,
    [SkillProcessor.MODIFY_ON_DIRECTION]: (params) => `如果按下${params[0]}键, 追加动作 _${params[0]} `,
    [SkillProcessor.MODIFY_ON_SUMMON_COUNT]: (params) => modifyOnMonsterCount(params),
};

function modifyOnBubbleCount(params: string[]): string {
    let ret: string[] = [];
    const split = params[0].split(';');
    for (let i = 0; i < split.length / 3; ++i) {
        const j = i * 3;
        const bubbleId = split[j];
        const bubbleCount = split[j + 1];
        const actionName = split[j + 2];
        const fragment = `泡泡ID ${bubbleId} ${bubbleCount}个时 , 执行动作 ${actionName}`;
        ret.push(fragment);
    }

    return ret.join('; ');
}


function modifyOnMonsterCount(params: string[]): string {
    let ret: string[] = [];
    const split = params[0].split(';');
    for (let i = 0; i < split.length / 3; ++i) {
        const j = i * 3;
        const monsterId = split[j];
        const monsterCount = split[j + 1];
        const actionName = split[j + 2];
        const fragment = `当有${monsterCount}个 怪物ID ${monsterId}时, 执行动作 ${actionName}`;
        ret.push(fragment);
    }

    return ret.join('; ');
}
export default Vue.extend({
    props: {
        processors: {
            type: Array as () => number[],
        },
        processParams: {
            type: Array as () => string[],
        },
    },
    data(): IData {
        return {
            procs: [],
        };
    },
    watch: {
        async processors() {
            await this.evaluate();
        },
        async processParams() {
            await this.evaluate();
        },
    },
    async mounted() {
        await this.evaluate();
    },
    methods: {
        async evaluate() {
            this.procs = await this.calcProcs();
        },
        async calcProcs(): Promise<IProcessor[]> {
            const ret: IProcessor[] = [];
            let paramIdx = 0;
            for (let i = 0; i < this.processors.length; ++i) {
                const processorId = this.processors[i];
                if (processorId === SkillProcessor.NULL) {
                    continue;
                }

                switch (processorId) {
                    case SkillProcessor.DO_ACTION:
                    case SkillProcessor.AURA:
                    case SkillProcessor.DIVIDED_STATE_EFFECT_ARG:
                        const val = this.processParams[paramIdx++];
                        if (val != "0" && val) {
                            ret.push({
                                type: processorId,
                                params: [val],
                            });
                        }
                        break;
                    case SkillProcessor.CHANGE_PROJECTILE:
                        ret.push({
                            type: processorId,
                            params: [
                                this.processParams[paramIdx++],
                                this.processParams[paramIdx++],
                            ],
                        });
                        break;
                    case SkillProcessor.APPLY_RANGED_STATE_EFFECT:
                        ret.push({
                            type: processorId,
                            params: [
                                this.processParams[paramIdx++],
                                this.processParams[paramIdx++],
                                this.processParams[paramIdx++],
                                this.processParams[paramIdx++],
                            ],
                        });
                        break;
                    case SkillProcessor.DO_LOOPING_ACTION:
                        ret.push({
                            type: processorId,
                            params: [
                                this.processParams[paramIdx++],
                                this.processParams[paramIdx++],
                                this.processParams[paramIdx++],
                                this.processParams[paramIdx++],
                                this.processParams[paramIdx++],
                            ],
                        });
                        break;
                    case SkillProcessor.UNKNOWN16:
                        ret.push({
                            type: processorId,
                            params: [
                                this.processParams[paramIdx++],
                                this.processParams[paramIdx++],
                                this.processParams[paramIdx++],
                                this.processParams[paramIdx++],
                                this.processParams[paramIdx++],
                                this.processParams[paramIdx++],
                                this.processParams[paramIdx++],
                                this.processParams[paramIdx++],
                            ],
                        });
                        break;
                    case SkillProcessor.ADD_ACTION_SUFFIX:
                        const params: string[] = [];
                        for (; paramIdx < this.processParams.length; ++paramIdx) {
                            const v = this.processParams[paramIdx];
                            if (v == "0" || !v) {
                                --paramIdx;
                                break;
                            }

                            params.push(v);
                        }
                        ret.push({
                            type: processorId,
                            params,
                        });
                        break;
                    case SkillProcessor.APPLY_RANGED_STATE_EFFECT:
                        ret.push({
                            type: processorId,
                            params: [
                                this.processParams[paramIdx++],
                                this.processParams[paramIdx++],
                                this.processParams[paramIdx++],
                                this.processParams[paramIdx++],
                            ],
                        });
                        break;
                    case SkillProcessor.CTC:
                    case SkillProcessor.MODIFY_ON_DIRECTION:
                    case SkillProcessor.MODIFY_ON_SUMMON_COUNT:
                    case SkillProcessor.MODIFY_ACTION_ON_BUBBLE_COUNT:
                    case SkillProcessor.MODIFY_STATE_EFFECT_ON_BUBBLE_COUNT:
                    case SkillProcessor.IMPACT_BLOW:
                    case SkillProcessor.SUMMON_DO_ACTION:
                        ret.push({
                            type: processorId,
                            params: [
                                this.processParams[paramIdx++],
                            ],
                        });
                        break;
                }
            }

            for (let v of ret) {
                const res = (processorMap[v.type] && processorMap[v.type](v.params)) || `Unk${v.type} ${v.params[0]}`;
                let resStr = '';
                if (typeof res === 'string') {
                    resStr = res;
                } else {
                    resStr = await (res as Promise<string>);
                }

                v.desc = resStr;
                v.params = v.params.filter(p => p != null);
            };

            return ret;
        },
    }
})
