












import Vue from 'vue'
import { SkillUsableChecker } from '../../models/skills/SkillEnums';

interface IChecker {
    type: SkillUsableChecker;
    params: string[];
    desc?: string;
}

function op2Str(op: string): string {
    switch (op) {
        case "<": return "少于";
        case "":
        case "==": return "是";
        case "!=": return "不能是";
        case ">": return "大于";
        case "<=": return "至多";
        case ">=": return "至少";
        case "&": return "是";
        case "!&": return "不能是";
        default: return op;
    }
}

function op2Str_action(op: string): string {
    switch (op) {
        case "Air": return "浮空";
        case "Down": return "倒地";
        case "Hit": return "被击中";
        case "Attack": return "攻击状态";
        case "Stun": return "眩晕";
        default: return op;
    }
}

const checkerMap: {[k: number /* SKillUsableChecker */]: (params: string[]) => string|Promise<string>} = {
    [SkillUsableChecker.CASTER_STATE]: (params) => `施法者当前动作${op2Str(params[1])} '${op2Str_action(params[0])}'`,
    [SkillUsableChecker.HAS_HP]: (params) => `施法者当前血量 ${op2Str(params[1])} ${params[0]}%`,
    [SkillUsableChecker.PROBABILITY]: (params) => `触发概率 ${params[0]}%`,
    [SkillUsableChecker.TARGET_STATE]: (params) => `目标不超过 ${params[2]} 个单位且目标当前动作 ${op2Str(params[1])} '${op2Str_action(params[0])}' `,
    [SkillUsableChecker.CAN_MOVE]: () => `施法者能移动`,
    [SkillUsableChecker.CAN_JUMP]: () => `施法者能跳`,
    [SkillUsableChecker.IS_HIT]: () => `施法者正被攻击`,
    [SkillUsableChecker.TOGGLE_IS_ACTIVE]: async (params): Promise<string> => `切换技能 ${params[0]} 当前是激活的`, // todo
    [SkillUsableChecker.ENERGY_CHARGE]: (params) => `能量充值到 ${params}`,
    [SkillUsableChecker.CAN_GROUND_MOVE]: () => `施法者能行走`,
    [SkillUsableChecker.TARGET_IN_RANGE]: (params) => `目标在 ${params} 范围内`,
    [SkillUsableChecker.HAS_BUBBLES]: async (params): Promise<string> => `施法者至少有${params[1]}个 泡泡 泡泡ID ${params[0]}`, // todo
    [SkillUsableChecker.SUMMON_IN_RANGE]: (params) => `召唤 ${params} 在可及范围内`,
    [SkillUsableChecker.TARGET_STATE_EFFECT]: async (params) => `使用目标有 ${params} 动作效果`,
};

interface IData {
    checkers: IChecker[];
}

export default Vue.extend({
    props: {
        usableCheckers: {
            type: Array as () => SkillUsableChecker[],
        },
        canUseParams: {
            type: Array as () => string[],
        },
    },
    data(): IData {
        return {
            checkers: [],
        };
    },
    watch: {
        async usableCheckers() {
            await this.evaluate();
        },
        async canUseParams() {
            await this.evaluate();
        },
    },
    async mounted() {
        await this.evaluate();
    },
    methods: {
        async evaluate() {
            this.checkers = await this.calcCheckers();  
        },
        async calcCheckers(): Promise<IChecker[]> {
            let ret: IChecker[] = [];
            let paramIdx = 0;
            for (let i = 0; i < this.usableCheckers.length; ++i) {
                const checker = this.usableCheckers[i];
                switch (checker) {
                    case SkillUsableChecker.CAN_MOVE:
                    case SkillUsableChecker.CAN_JUMP:
                    case SkillUsableChecker.IS_HIT:
                    case SkillUsableChecker.ENERGY_CHARGE:
                    case SkillUsableChecker.CAN_GROUND_MOVE:
                    case SkillUsableChecker.TARGET_IN_RANGE:
                        ret.push({
                            type: checker,
                            params: [],
                        });
                        break;
                    case SkillUsableChecker.PROBABILITY:
                    case SkillUsableChecker.TOGGLE_IS_ACTIVE:
                    case SkillUsableChecker.SUMMON_IN_RANGE:
                        ret.push({
                            type: checker,
                            params: [this.canUseParams[paramIdx++]],
                        });
                        break;
                    case SkillUsableChecker.CASTER_STATE:
                    case SkillUsableChecker.HAS_HP:
                    case SkillUsableChecker.HAS_BUBBLES:
                    case SkillUsableChecker.TARGET_STATE_EFFECT:
                        ret.push({
                            type: checker,
                            params: [this.canUseParams[paramIdx++], this.canUseParams[paramIdx++]],
                        });
                        break;
                    case SkillUsableChecker.TARGET_STATE:
                        ret.push({
                            type: checker,
                            params: [this.canUseParams[paramIdx++], this.canUseParams[paramIdx++], this.canUseParams[paramIdx++]],
                        });
                        break;
                    case SkillUsableChecker.NULL:
                    default:
                        continue;
                }
            }

            for (let v of ret) {
                const res = (checkerMap[v.type] && checkerMap[v.type](v.params)) || '';
                let resStr = '';
                if (typeof res === 'string') {
                    resStr = res;
                } else {
                    resStr = await (res as Promise<string>);
                }

                v.desc = resStr;
                v.params = v.params.filter(p => p != null);
            }

            // Filter checkers that have all undefined params
            ret = ret.filter((v) => v.params.length && 
                v.params.reduce((prev, cur) => cur !== undefined ? cur : prev, undefined as any) != undefined);

            return ret;
        }
    }
})
